	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"But you're not white","date":"08 Nov 2004","slug":"/2004-11-08-but-youre-not-white/","tags":"Y2004"},"html":"<p><a href=\"http://www.neilarmstrong.de/weblog/2004_11_01_archive.html#109976265621517065\" title=\"German For Beginners: Where is that, exactly?\">German For Beginners:</a> \"Whenever a person asks V where she's from and she says that she's English and was born in London, I'm amazed at the number who look at her and ask \"Ja, aber woher kommen Sie wirklich?\" - \"where are you from, really?\" I think it's the outright, unabashed, \"but, but you're not white\", inherent in the statement.\" Funny, that never happens to me. But then again, Germans were never known for subtlety.</p>"},"next":{"fields":{"title":"High Volume","date":"07 Nov 2004","slug":"/2004-11-07-high-volume/"}},"previous":{"fields":{"title":"Firefox not ready","date":"08 Nov 2004","slug":"/2004-11-08-firefox-not-ready/"}}},{"node":{"fields":{"title":"High Volume","date":"07 Nov 2004","slug":"/2004-11-07-high-volume/","tags":"Y2004"},"html":"<p>I recently purchased an iPod, but was disappointed with the sound. It was simply not loud enough. I took the the following steps:</p>\n<p>1. Applied <a title=\"iPod Volume Booster (English)\" href=\"http://volumebooster.tangerine-soft.de/\">iPod Volume Booster</a> to remove the EU volume restriction.<br />\n2. Purchased a set of <a href=\"http://www.minidisc.com.au/product_info.php?products_id=421\">plug-style earphones</a>.<br />\n3. Imported appropriate music (Ramones, Donnas, Ärzte, etc.).</p>\n<p>Ah. Much better.</p>"},"next":{"fields":{"title":"The Values-Vote Myth","date":"06 Nov 2004","slug":"/2004-11-06-the-valuesvote-myth/"}},"previous":{"fields":{"title":"But you're not white","date":"08 Nov 2004","slug":"/2004-11-08-but-youre-not-white/"}}},{"node":{"fields":{"title":"The Values-Vote Myth","date":"06 Nov 2004","slug":"/2004-11-06-the-valuesvote-myth/","tags":"Y2004"},"html":"<p><a href=\"http://www.nytimes.com/2004/11/06/opinion/06brooks.html?ex=1257483600&en=b4613533d9a1bdde&ei=5090&partner=rssuserland\">David Brooks (in NYT): The Values-Vote Myth</a> \"Just listen to some coastal and university town liberals talk about how conformist and intolerant people in Red America are. It makes you wonder: why is it that people who are completely closed-minded talk endlessly about how open-minded they are?\" (Via <a href=\"http://www.kottke.org/remainder/04/11/6873.html\">kottke</a>)</p>"},"next":{"fields":{"title":"Sydney and Hannover","date":"06 Nov 2004","slug":"/2004-11-06-sydney-and-hannover/"}},"previous":{"fields":{"title":"High Volume","date":"07 Nov 2004","slug":"/2004-11-07-high-volume/"}}}],"pathPrefix":"","first":false,"last":false,"index":388,"pageCount":1101}}

